___CSS_LOADER_EXPORT___.push([module.id, `.accordion {
  height: var(--heightAccordion);
  border: 1px solid #d7d7d7;
  border: 1px solid var(--secondary-s-color, #d7d7d7);
  overflow: hidden;
  transition: all 0.2s;
}
.accordion_active .accordion__arrow {
  transform: rotate(-180deg);
}
.accordion_active .accordion__header {
  padding-bottom: 0;
}
.accordion_active .accordion__descr {
  display: block;
  opacity: 1;
}
.accordion__header {
  padding: 20px 30px 20px;
  display: flex;
  gap: 15px;
  justify-content: space-between;
  cursor: pointer;
  transition: all 0.2s;
}
@media (max-width: 640px) {
  .accordion__header {
    padding: 20px;
  }
}
.accordion__title {
  color: #41456b;
  color: var(--primary---content, #41456b);
  font-size: 20px;
  font-weight: 600;
  line-height: 25px; /* 125% */
}
@media (max-width: 640px) {
  .accordion__title {
    font-size: 18px;
  }
}
.accordion__arrow {
  height: 24px;
  transition: all 0.2s;
}
.accordion__descr {
  padding: 0 95px 33px 30px;
  margin-top: 10px;
  display: none;
  color: #606276;
  color: var(--secondary---text, #606276);
  font-size: 16px;
  font-weight: 400;
  line-height: 180%; /* 28.8px */
  letter-spacing: 0.32px;
  opacity: 0;
  transition: all 0.2s;
}
@media (max-width: 640px) {
  .accordion__descr {
    padding: 0 20px 20px 20px;
  }
}`, "",{"version":3,"sources":["webpack://./src/UI/accordion/index.scss"],"names":[],"mappings":"AAAA;EACE,8BAAA;EACA,yBAAA;EAAA,mDAAA;EACA,gBAAA;EACA,oBAAA;AACF;AAEI;EACE,0BAAA;AAAN;AAEI;EACE,iBAAA;AAAN;AAEI;EACE,cAAA;EACA,UAAA;AAAN;AAIE;EACE,uBAAA;EACA,aAAA;EACA,SAAA;EACA,8BAAA;EACA,eAAA;EACA,oBAAA;AAFJ;AAII;EARF;IASI,aAAA;EADJ;AACF;AAIE;EACE,cAAA;EAAA,wCAAA;EAEA,eAAA;EACA,gBAAA;EACA,iBAAA,EAAA,SAAA;AAHJ;AAKI;EAPF;IAQI,eAAA;EAFJ;AACF;AAKE;EACE,YAAA;EACA,oBAAA;AAHJ;AAME;EACE,yBAAA;EACA,gBAAA;EACA,aAAA;EACA,cAAA;EAAA,uCAAA;EAEA,eAAA;EACA,gBAAA;EACA,iBAAA,EAAA,WAAA;EACA,sBAAA;EACA,UAAA;EAEA,oBAAA;AANJ;AAQI;EAdF;IAeI,yBAAA;EALJ;AACF","sourcesContent":[".accordion {\n  height: var(--heightAccordion);\n  border: 1px solid var(--secondary-s-color, #d7d7d7);\n  overflow: hidden;\n  transition: all 0.2s;\n\n  &_active {\n    .accordion__arrow {\n      transform: rotate(-180deg);\n    }\n    .accordion__header {\n      padding-bottom: 0;\n    }\n    .accordion__descr {\n      display: block;\n      opacity: 1;\n    }\n  }\n\n  &__header {\n    padding: 20px 30px 20px;\n    display: flex;\n    gap: 15px;\n    justify-content: space-between;\n    cursor: pointer;\n    transition: all 0.2s;\n\n    @media (max-width: 640px) {\n      padding: 20px;\n    }\n  }\n\n  &__title {\n    color: var(--primary---content, #41456b);\n\n    font-size: 20px;\n    font-weight: 600;\n    line-height: 25px; /* 125% */\n\n    @media (max-width: 640px) {\n      font-size: 18px;\n    }\n  }\n\n  &__arrow {\n    height: 24px;\n    transition: all 0.2s;\n  }\n\n  &__descr {\n    padding: 0 95px 33px 30px;\n    margin-top: 10px;\n    display: none;\n    color: var(--secondary---text, #606276);\n\n    font-size: 16px;\n    font-weight: 400;\n    line-height: 180%; /* 28.8px */\n    letter-spacing: 0.32px;\n    opacity: 0;\n\n    transition: all 0.2s;\n\n    @media (max-width: 640px) {\n      padding: 0 20px 20px 20px;\n    }\n  }\n}\n"],"sourceRoot":""}]);
